    padding-left: 10px;
    background: rgba(40, 75, 99, 1);
  }
  .container {
    display: block;
    width: 75%;
    min-width: calc(100% - 230px);
    min-width: -moz-calc(100% - 230px);
    box-sizing: border-box;
    float: right;
    margin: 0 auto;
    padding: 0;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
  }
  .burger {
    display: none;
  }
  .Aside {
    display: block;
